import ReactCardFlip from 'react-card-flip';
import { Link } from 'react-router-dom';

// material-ui
import { Box, Card, Grid, Typography, CircularProgress, CardContent } from '@mui/material';

// project imports
import SubCard from 'ui-component/cards/SubCard';
import MainCard from 'ui-component/cards/MainCard';
import SecondaryAction from 'ui-component/cards/CardSecondaryAction';
import { gridSpacing } from 'store/constant';

// ==============================|| TYPOGRAPHY ||============================== //
function checkCardProgres(num) {
    let procent = 0;

    let appProp = JSON.parse(localStorage.getItem('appProp'));

    if (appProp == null) {
        localStorage.setItem('appProp', JSON.stringify({ favStorage: [], checkedList: [] }));
        appProp = JSON.parse(localStorage.getItem('appProp'));
    }

    let checkedList = appProp.checkedList;

    if (!checkedList) {
        checkedList = [];
    }

    let progresCounter = 0;

    for (let index = 0; index < checkedList.length; index++) {
        if ((num - 1) * 20 < checkedList[index] && checkedList[index] < (num - 1) * 20 + 20) {
            progresCounter++;
        }
    }

    procent = (progresCounter / 20) * 100;
    console.log(procent);
    return procent;
}

function CircularProgressWithLabel(props) {
    return (
        <Box sx={{ position: 'relative', display: 'inline-flex' }}>
            <CircularProgress variant="determinate" {...props} />
            <Box
                sx={{
                    top: 0,
                    left: 0,
                    bottom: 0,
                    right: 0,
                    position: 'absolute',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center'
                }}
            >
                <Typography variant="caption" component="div" color="text.secondary">
                    {`${Math.round(props.value)}%`}
                </Typography>
            </Box>
        </Box>
    );
}

function createUnitList() {
    let unitList = [];

    for (let unitNumber = 1; unitNumber <= 1000 / 20; unitNumber++) {
        unitList.push(
            <Grid item xs={6} key={unitNumber}>
                <Link
                    to="/sliding"
                    state={{
                        from: (unitNumber - 1) * 20,
                        to: (unitNumber - 1) * 20 + 20
                    }}
                    style={{ textDecoration: 'none' }}
                >
                    <Card variant="outlined">
                        <CardContent>
                            <Grid container direction="column" alignItems="center">
                                <Grid item>
                                    <Typography mb={1} variant="h4" sx={{ textDecoration: 'none' }}>
                                        {'Unit' + ' ' + unitNumber}
                                    </Typography>
                                    <CircularProgressWithLabel value={checkCardProgres(unitNumber)}></CircularProgressWithLabel>
                                </Grid>
                            </Grid>
                        </CardContent>
                    </Card>
                </Link>
            </Grid>
        );
    }

    return unitList;
}

const CardMenu = () => (
    <MainCard title="Card menu">
        <Grid container spacing={gridSpacing}>
            {createUnitList()}
        </Grid>
    </MainCard>
);

export default CardMenu;
